@import url(https://fonts.googleapis.com/css2?family=Love+Ya+Like+A+Sister&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Love+Ya+Like+A+Sister&family=Sanchez&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --yellow: #FFC75F;
    --yellow_dark: #E7B355;
    --purple: #845EC2;
    --lightpurple: #a98ddb;
    --light_grey: #EFEFEF;
    --grey: #969696;
}

*,
*:after,
*:before {
    box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
}

body {
    font-size: 65.2%;
}

h1 {
    display: inline-block;
    font-family: 'Love Ya Like A Sister';
    color: white;
    font-weight: lighter;
    text-rendering: geometricPrecision;
    --minFontSize: 2rem;
    --maxFontSize: 8rem;
    --scaler: 4vw;
    font-size: clamp(var(--minFontSize), var(--scaler), var(--maxFontSize));
}

h2 {
    display: inline-block;
    font-family: 'Sanchez';
    color: white;
    font-weight: lighter;
    text-rendering: geometricPrecision;
    --minFontSize: 2rem;
    --maxFontSize: 4rem;
    --scaler: 3vw;
    font-size: clamp(var(--minFontSize), var(--scaler), var(--maxFontSize));

}

p {
    font-size: calc(1.5rem+1vw);
    font-family: 'Sanchez';
}




.miniHeader {
    position: relative;
    display: block;
    font-family: 'Love Ya Like A Sister';
    color: white;
    font-weight: lighter;
    text-rendering: geometricPrecision;
    padding: 1rem;
    font-size: 1rem;
    margin-left: calc(45vw - 25%);
}

/*header component*/
.headerActive {
    position: relative;
    background-color: var(--yellow);
    width: 100vw;
    height: 30rem;
    transition: height 0.15s ease-out;
    overflow: hidden;
}
.headerActive.shrink{
    height: 15rem!important;
    transition: height 1s ease-out;
   
}

hr.divider {
    border: none;
    border-bottom: 0.1rem solid var(--yellow_dark);
}

.lhwrap {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    padding-top: 4rem;

}
.lhwrap.gone{
    padding-top: 15rem!important;
    transition: padding-top 1s ease-out;
}

.hwrap {
    display: block;
    position: relative;
    height: 2rem;

}

.logowrap {
    position: relative;
    height: 40%;
    width: 10%;
    display: block;
    background-image: url(/static/media/Splash.b42a884f.svg);
    background-repeat: no-repeat;
    background-size: contain;
    margin: 0 1rem 0 0;

}

.logoimg {
    position: relative;
    width: 100%;
    height: auto;
    top: 5%;
}

.hwrap {
    display: inline-block;
}
.profilePagewrap {
    position: absolute;
    top: 3.3rem;
    right: -15rem;
    background-color: var(--yellow_dark);
    color: white;
    height: calc(100vh - 3.3rem);
    width: 15rem;

    font-family: 'Sanchez';
    box-shadow: inset 7px 0 9px -7px rgba(0, 0, 0, 0.7);
}
.popout{
    right: 0rem;
    transition: right 0.5s ease-out;
}

.popin{
    right: -15rem;
    transition: right 0.5s ease-out;
}

.profilePagewrap h6 {
    text-align: center;
    padding: 1rem;
    font-size: 0.7rem;
}

.profilePagewrap ul {
    padding: 1rem;
}

.profilePagewrap ul li {
    list-style-type: none;
}

.logoutwrap {
    position: absolute;
    bottom: 0vh;
    display: block;
    width: 100%;
    height: 4rem;
    color: #EFEFEF;
}

.logout {
    text-align: center;
    margin: 0 auto;
    padding: 1rem;
    width: 50%;
    border-radius: 4rem;
    background-color: #583591;
}
.outputwrap {
    display: block;
    position: relative;
    margin: 0 auto;
    margin-top: 3rem;
    width: 45rem;
    height: 20rem;
    background-color: #845EC2;
    border-radius: 1rem;
    box-shadow: 0 0 0.5rem 0 rgba(0, 0, 0, 0.5);

}

.outputwhitewrap {
    display: block;
    position: relative;
    margin: 0 auto;
    width: 45rem;
    height: 18rem;
    background-color: #fcfcfc;
    border-radius: 1rem 1rem 0 0;
    overflow: scroll;
    text-align: center;
}
.hands{
    position: relative;
    display: inline-block;
    width: 4.7rem;
    height: 4.7rem;
    overflow: hidden;
}
.hands>img{
    position: relative;
    width: 37.5rem;
    height: 18.75rem;
}

[hand-data]:before {
	content: attr(hand-data);
    position: absolute;
    top:4rem;
    left:2rem;
    z-index: 100;
}
.inputwrap {
    display: block;
    position: relative;
    margin: 0 auto;
    margin-top: -5.5rem;
    width: 45rem;
    height: 10rem;
    background-color: var(--purple);
    border-radius: 1rem;
    box-shadow: 0 0 0.5rem 0 rgba(0, 0, 0, 0.5);

}
.inputwrap.nobg1 {
    background-color:transparent;
    transition: background-color 0.2s ease-out;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    transition: box-shadow 0.2s ease-out;
    margin-top: -10rem;
    transition: margin-top 0.2s ease-out;
}

.inputwhitewrap {
    display: block;
    position: relative;
    margin: 0 auto;
    margin-top: -5.5rem;
    width: 45rem;
    height: 8rem;
    background-color: #fcfcfc;
    border-radius: 1rem 1rem 0 0;
}

.inputwhitewrap.nobg2 {
    background-color:transparent;
    transition: background-color 0.5s ease-out;
}


.formwrap {
    width: 80%;
    height: 70%;
    padding-top: 2rem;
    margin: 0 auto;
}

.form-input {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    background:var(--light_grey);
    border: 0.2rem solid var(--grey);
    font-size: 1.4em;
    padding: .4em .7em;
    position: relative;
    border-radius: 4rem;


}

.input {
    background: none;
    position: relative;
    float: left;
    border: none;
    display: block;
    height: 100%;
    width: 83%;
    padding-left: 0.5em;
    padding-right: 1.4em;
    font-family: 'Sanchez';
    font-size: 150%;
}
input:focus {
    outline:none;
}

.iconl {
    left: 0;
    position: relative;
    float: left;
}
.iconl::after {
    content: "|";
    position: relative;
    margin-left: 0.2rem;
    top: -0.2rem;
    font-size: 1.5rem;
    color: var(--grey);
}
.iconr {
    right: -0.60rem;
    position: relative;
    float: right;
    top: -0.35rem;
    cursor: pointer;
}

.fa-keyboard {
    font-size: 200%;
    margin-top: 0.2em;
    color: var(--grey);
}


.fa-arrow-alt-circle-right {
    color: var(--purple);
    font-size: 350%;
    padding: 0;
    line-height: 0rem;

}
.fa-arrow-alt-circle-right:hover {
color: var(--lightpurple);

}

.profilewrap{
    position: absolute;
    right:2rem;
    top: 0.2rem;
    display: flex;
    justify-content:flex-end;
    min-width:10rem;
    height: 14%;
    cursor: pointer;
}
.profileName{
    position: relative;
    padding: 0.3rem;
    padding-right:2.5rem;
    padding-left: 1rem;
    background-color: var(--yellow_dark);
    border-radius: 2rem;
    margin-top: 0.8rem;
    height: 1.5rem;
    left: 2rem;
    text-align: left;
    white-space: nowrap;
    font-family: 'Sanchez';
    color: white;

}
.profileImage{
    position: relative;
    z-index: 2;
    border: 0.2rem solid white;
    width: 3rem;
    height: 3rem;
    border-radius: 100%;
    float: left;
    background-color:var(--yellow_dark) ;
    background-image: url(/static/media/default-avatar.44498ef3.png);
    background-size: contain;
    background-repeat: no-repeat;
}

